/* new css */
body {
  font-size: 12px;
  color: black;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  font-family: sans-serif;
  
}

a{
  text-decoration: none;
}

table tr td{
  text-align: center;
}


#home {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(45deg, #00FFBA, #6890D8, #2c67d4, #9906a3);
  background-size: 400% 400%;
  animation: gradient 10s ease infinite;
  color: white;
}

.logo ul {
  display: inline-flex;
  margin: 3%;
}

.logo ul li {
  list-style: none;
  position: relative;
  text-align: center;
  margin: 0 auto;
  color: white;
  padding: 7%;
}

.logo ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.5em;
}

.logo ul li a:hover {
  color: #9906a3;
}

.logo ul li a:visited {
  text-decoration: none;
}


html{
  scroll-behavior: smooth; 
}

.maintext .text {
  text-align: left;
  padding-top: 20px;
  padding-left: 3em;
  font-size: 3em;
}

.maintext .text img{
  width: 50%;
}


.maintext .text p {
  font-size: 20px;
}


.learn {
  font-size: 20px;
  position: absolute;
  text-decoration: 0;
  color: white;
  border: 2px solid white;
  border-radius: 50px;
  padding: 20px;
}

.buy {
  font-size: 20px;
  text-decoration: 0;
  color: white;
  border: 2px solid white;
  border-radius: 50px;
  padding: 20px;
}

.learn:hover {
  font-size: 20px;
  position: absolute;
  text-decoration: 0;
  color: #9906a3;
  border: 2px solid #9906a3;
  background-color: rgb(0, 0, 0, 0.1);
  border-radius: 50px;
  padding: 20px;
}

#about {
  width: 100vw;
  height: 100vh;
  background-color: white;
  padding-top: 30em;
}



#about h1 {
  color: #846E52;
  font-size: 7em;
}

#about h2 {
  color: #DBC6AA;
  font-size: 3em;
}


.percentage{
  font-size: 1vw;;
}

h12{
  animation: color-change 4s infinite;
  font-size: 3vw;
}

.headerPerc{
  animation: color-change 4s infinite;
  font-size: 3vw;
}

.percentage table{
  padding: 5vw;
}

.rightImage img {
  width: 30%;
  height: 10%;
  float: right;
  overflow: hidden;
  padding-top: 10%;
  padding-right: 5%;
  transform: translatey(0px);
  animation: float 6s ease-in-out infinite;
}

#community {
  width: 100vw;
  height: 50vh;
  background-color: burlywood;
  padding-top: 10em;
}

#community h1{
  font-size: 300%;;
}


.leftImage img {
  width: 20%;
  height: 10%;
  float: left;
  overflow: hidden;
  transform: translatey(0px);
  animation: float 6s ease-in-out infinite;
}

footer {
  background-color: rgb(66, 54, 38);
  color: white;
  padding: 30px;
  text-align: center;
}

#about img,
#buy img{
  padding: 5%;
  width: 20%;
  height: 20%;
}

@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@-webkit-keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@keyframes color-change {
  0% {
      color: #9906a3;
  }
  50% {
      color: #6890D8;
  }
  100% {
      color: #00FFBA;
  }
  50% {
      color: #d868ad;
  }
}

@-webkit-keyframes color-change {
  0% {
      color: #9906a3;
  }
  50% {
      color: #6890D8;
  }
  100% {
      color: #00FFBA;
  }
  50% {
      color: #d868ad;
  }
}

@keyframes float {
  0% {
      transform: translatey(0px);
  }
  50% {
      transform: translatey(-100px);
  }
  100% {
      transform: translatey(0px);
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  #home ul img {
      width: 30%;
      height: 100%;
  }
  .logo ul {
      display: inline-flex;
      margin-left: -10px;
  }
  .logo ul li {
      list-style: none;
      position: relative;
      text-align: center;
      margin: 0 auto;
      color: white;
  }

}

@media (min-width: 481px) and (max-width: 767px) {
  #home ul img {
      width: 40%;
      height: 100%;
  }
  .logo ul {
      display: inline-flex;
      margin-left: -6%;
  }
  .logo ul li {
      padding: 0.7em;
  }
  .maintext .text {
      text-align: left;
      font-size: 2em;
  }

}

@media (min-width: 300px) and (max-width: 480px) {
  .logo ul .logo {
      width: 100%;
  }
  .logo ul {
      display: inline-flex;
      margin-left: -7%;
  }
  .logo ul li {
      padding: 0.2em;
  }
  .maintext .text {
      text-align: left;
      font-size: 2.5em;
  }

  #mid {
      padding-left: 0;
      font-size: 0.67em;
  }
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}









